/* --- About Page -------------------------------- */

.About {
    width: 100%;
    background-color: #141414;
}

.AboutWrapper {
    width: 100%;
    height: 100%;
    padding: 0 10%;
    position: relative;
}

        .AboutTitle {
        margin-top: 100px;
        font-size: 75px;
        font-weight: 900;
        font-family: aktiv-grotesk-condensed, sans-serif;
        text-transform: uppercase;
        transition: all .5s ease-in-out;
        }
        .AboutTitleThin {
            font-size: 75px;
            font-weight: 200;
            font-family: aktiv-grotesk-condensed, sans-serif;
            text-transform: uppercase;
            margin-bottom: 80px;
            transition: all .5s ease-in-out;
        }



.AboutContentWrapper {
    width: 600px;
    padding-left: 10%;
    margin-bottom: 150px;
    
}

    .AboutSection {
        width: 100%;
        margin-bottom: 110px;
        transition: all .5s ease-in-out;
    }

        .AboutSectionTitle {
            font-size: 40px;
            font-weight: 900;
            font-family: aktiv-grotesk-condensed, sans-serif;
            text-transform: uppercase;
            margin-bottom: 20px;
            transition: font-size .5s ease-in-out;
        }

        .AboutSectionSubtitle {
            font-size: 20px;
            margin-bottom: 10px;

        }
            .AboutSectionSubtitle2 {
                margin-top: 40px;
            }

        .AboutSectionText {
            font-size: 16px;
            line-height: 30px;
            text-transform: capitalize;
        }

            .AboutSectionTextDark {
                color: #828282;
            }


@media screen and (max-width: 760px){
    .AboutSection {
        margin-bottom: 70px;
    }
    .AboutContentWrapper {
        width: 100%;
        padding-left: 5%;
        transition: all .5s ease-in-out;
    }
    .AboutTitle {
        font-size: 50px;
    }
    .AboutTitleThin {
        font-size: 50px;
    }
    .AboutSectionTitle {
        font-size: 24px;
    }
    .AboutSectionSubtitle {
        font-size: 18px;
    }
    .AboutSectionText {
        font-size: 12px;
    }
    .AboutSectionTextDark {
        font-size: 14px;
    }

}